.movies-cardlist {
  margin-left: 70px;
  margin-right: 70px;
  font-family: "Inter", Arial, sans-serif;
}

.movies-cardlist__container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 70px 0 50px;
  display: grid;
  grid-template-columns: repeat(4, 270px);
  gap: 20px;
  list-style: none;
  justify-content: center;
}

.movies-cardlist__container_dis {
  display: none;
}

.movies-cardlist__alert {
  margin: 0;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.05;
  text-align: center;
  color: #ffffff;
}

.movies-cardlist__alert_dis {
  display: none;
}


.movies-cardlist__button {
  width: 100%;
  max-width: 320px;
  min-height: 36px;
  margin: auto;
  margin-bottom: 100px;
  display: flex;
  justify-content: center;
  background: #2F2F2F;
  border: none;
  border-radius: 6px;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.05;
  display: flex;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  transition: opacity 0.5s ease;
  cursor: pointer;
}

.movies-cardlist__button_dis {
  display: none;
}

.movies-cardlist__button:hover {
  opacity: 0.7;
}

@media (max-width: 1279px) {
  .movies-cardlist {
    margin-left: 30px;
    margin-right: 30px;
  }  
  .movies-cardlist__container {
    grid-template-columns: repeat(2, 339px);
  }
}

@media (max-width: 767px) {
  .movies-cardlist {
    margin-left: 10px;
    margin-right: 10px;
  }
  .movies-cardlist__container {
    grid-template-columns: repeat(2, 220px);
  }
  .movies-cardlist__button {
    margin-bottom: 80px;
  }
}

@media (max-width: 480px) {
  .movies-cardlist__container {
    grid-template-columns: repeat(1, 300px);
  }
}

@media (max-width: 400px) {
  .movies-cardlist {
    margin-left: 11px;
    margin-right: 11px;    
  }
  .movies-cardlist__container {
    gap: 35px;
    padding: 40px 0 50px;
  }
  .movies-cardlist__button {
    max-width: 240px;
    margin-bottom: 159px;
  }

}